	}
	.footer .showText{
		font-size: 14px;
	}
	.footer .pagination{
		float: right;
		display: inline-block;
	}
	.footer input{
		height: 18px;
		width: 43px;
		margin: 0px 6px;
  		text-align: center;
  		border-radius:5px;
	}
	.front,.end{
		width: 50px;
		display: inline-block;
		height: 12px;
	}
	.front img,.end img{
		display: inline-block;
		cursor: pointer;
	}
	.margin10{
		margin: 0 10px;
		width: 1px;
		height: 12px;
	}
	.arrow{
		width: 8px;
		height: 11px;
	}
	#deleteDay{
		margin: 0 10px;
		height: 30px;
		width: 45px;
		text-align: center;
		border-radius: 5px;
	}
	.autoDelete{
		float: left;
	}
	.search{
		float: right;
	}
	.search input{
		vertical-align: top;
	}
	.deleteBtnUnChoose{
		pointer-events: none;
		display: inline-block;
		width: 100px;
		height: 36px;
		background: url('../assets/unchoose.png');
		background-size: 100%;
		cursor: pointer;
		z-index: 1;
	}
	.deleteBtnChoose{
		z-index: 2;
		display: inline-block;
		width: 100px;
		height: 36px;
		background: url('../assets/choose.png');
		background-size: 100%;
		cursor: pointer;
	}
	.search .searchInput{
		background: url('../assets/search.png') no-repeat;
		width: 161px;
		height: 36px;
		background-size: 100%;
		background-position: center;
		text-indent:10px;
		display: inline-block;
		cursor: pointer;
		position: relative;
	}
	.search .searchInput input{
		border: none;
	    width: 107px;
	    top: 1px;
	    height: 32px;
	    outline: none !important;
	    position: absolute;
	}
	.el-date-editor.el-input, .el-date-editor.el-input__inner{
		height: 34px!important;
		width: 140px!important;
		margin-right: 15px;
		vertical-align: top;
	}
	.el-switch.is-checked .el-switch__core{
		height: 36px !important;
		width: 79px !important;
		vertical-align: top;
	}
	.background-color-#E5EFFA{
		background-color: #E5EFFA !important;
	}
	.filter2{
		filter: blur(2px);
		z-index: 1;
	}
	.changePasswordDiv{
		position: absolute;
		width: 305px;
		height: 468px;
		background: url('../assets/password.png') no-repeat;
		background-size: 100%;
		z-index: 4;
		top: 30px;
		right: 107px;
	}
	.displaynone{
		display: none;
	}
	.modal{
		position: absolute;
		z-index:3;
		top: 51px;
		bottom: 0px;
		left: 0px;
		right: 0px;
	}
	.changePasswordDiv p{
		position: absolute;
		top: 46px;
		left:116px;
		font-size: 17px;
		color: #0065CB;
	}
	.changePasswordDiv span{
		position: absolute;
		left: 37px;
		font-size: 14px;
		color: #999;
	}
	.changePasswordDiv input{
		position: absolute;
		left: 37px;
		outline: none;
		border: 1px solid #BCE0FD;
		height: 48px;
		width: 206px;
		background-color: #F1F9FF;
		border-radius: 10px;
		padding-left: 21px;
	}
	.changePasswordDiv .lastPas{
		top: 75px;
	}
	.changePasswordDiv .lastPasInput{
		top: 105px;
	}
	.changePasswordDiv .newPas{
		top: 170px;
	}
	.changePasswordDiv .newPasInput{
		top: 200px;
	}
	.changePasswordDiv .agNewPas{
		top: 265px;
	}
	.changePasswordDiv .agNewPasInput{
		top:295px;
	}
	.changePasswordDiv .changePasBtn{
		width: 224px;
		height: 48px;
		border: 1px solid #0065CB;
		position: absolute;
		top: 380px;
		left: 37px;
		border-radius: 10px;
		background-color: #0065CB;
		color: #fff;
		line-height: 48px;
		text-align: center;
		font-size: 14px;
		cursor: pointer;
	}
</style>

<template>
  <div class="index">
    	<div class="header">
    		<img src="../assets/logoIndex.png">
    		<span class="title">国美小虫·文件云</span>
    		<span class="right"><span class='userName'>{{userName}}，欢迎进入文件云</span><img src="../assets/user.png" @click="showPasswordDiv">
    		<span class='exit' @click="exit">退出</span></span>
    	</div>
    	<div :class="modalShow?'modal':'displaynone'" @click="hideModal"></div>
    	<div class="changePasswordDiv" v-show="isPasswordDivShow">
    		<p>修改密码</p>
    		<span class="lastPas">旧密码</span>
    		<input type="password" name="旧密码"  class="lastPasInput" placeholder="请输入旧密码" v-model="oldPassword">
    		<span class="newPas">新密码</span>
    		<input type="password" v-model="newPassword" name="新密码"  class="newPasInput" placeholder="请输入新密码">
    		<span class="agNewPas">再次输入新密码</span>
    		<input type="password" v-model="reNewPassword" name="重复新密码" class="agNewPasInput" placeholder="请再次输入新密码">
    		<div class="changePasBtn" @click="reset">修改</div>
    	</div>
    	<div :class="modalShow?'operation filter2':'operation'" placeholder="请输入旧密码">
    		<div class="autoDelete">
    			<span v-show="!isAutoDelete">智能删除</span>
    			<input v-show="!isAutoDelete" type="" name="day" v-model="autoDeleteDay" id="deleteDay">
    			<span v-show="!isAutoDelete">天前上传的文件</span>
    			<span v-show="isAutoDelete" style="color:#0065CB;line-height:36px;height:36px;font-size:17px">系统已开启删除30天前的文件功能！</span>
    			<el-switch style="margin-left:20px;margin-bottom:5px" v-model="isAutoDelete"
    			inactive-text="关" active-text="开" @change="autoDelete">
				</el-switch>
    		</div>
    		<div class="search">
    			<el-date-picker
			      v-model="start"
			      class="datePickerCss"
			      type="date"
			      placeholder="起始时间"
			      value-format="yyyy-MM-dd">
			    </el-date-picker>
			    <el-date-picker
			      v-model="end"
			      type="date"
			      class="datePickerCss"
			      placeholder="截止时间"
			      value-format="yyyy-MM-dd">
			    </el-date-picker>
    			<span class="searchInput" style="margin-right: 13px" @click="searchTable()"><input type="text" name="search" placeholder="输入用户名或其他" v-model="usercode" ></span>
    			
    			<div v-show="!isDelete" class="deleteBtnUnChoose"></div>
    			<div v-show="isDelete" class="deleteBtnChoose" @click="clickDeleteBtn"></div>
    		</div>
    	</div>
    	<table id="checkTable" cellspacing="0" cellpadding="0" :class="modalShow?' filter2':''">
    		<tr><th class="left background-f4"></th>
    			<th>编号</th><th>姓名</th><th>用户名</th><th>文件名称</th><th>大小</th><th>格式</th><th>上传时间</th><th><input type="checkbox" @change="chooseAll" :checked="choosenAll"></th><th class="right background-f4"></th>
    		</tr>
    		<tr v-for="(dataObj,index) in tableData"><td :class=oddOrNot(index,1)></td>
    			<td :class=oddOrNot(index)>{{index + 1 + (pageNumber-1)*pageSize}}</td><td :class=oddOrNot(index)>{{dataObj.username}}</td><td :class=oddOrNot(index)>{{dataObj.usercode}}</td><td :class=oddOrNot(index)>{{dataObj.filename}}</td><td :class=oddOrNot(index)>{{initFileSize(dataObj.length)}}</td><td :class=oddOrNot(index)>{{initStandard(dataObj.filename)}}</td><td :class=oddOrNot(index)>{{dataObj.createtime}}</td><td :class=oddOrNot(index)><input type="checkbox" class='deleteClass' @change="showDeleteBtn" :value="index"></td><td :class=oddOrNot(index,2)></td>
    		</tr>
    	</table>
    	<div :class="modalShow?'footer filter2':'footer'" >
    		<span class="showText">显示{{startPage}}至{{endPage}}条,共{{total}}条记录</span>
    		<div class="pagination">
    			<span class="front" style="margin-right: 5px">
    				<img src="../assets/firstPage.png" class="arrow" @click="homepage">
    				<img src="../assets/line.png" class='margin10'>
    				<img src="../assets/prePage.png" class="arrow" @click="prev">
    			</span>
    			<span>第</span><input type="text" v-model="pageNumber" @change="checkType"><span>共{{totalPage}}页</span>
    			<span class="end" style="margin-left: 20px">
    				<img src="../assets/nextPage.png" class="arrow" @click="next">
    				<img src="../assets/line.png" class='margin10'>
    				<img src="../assets/lastPage.png" class="arrow" @click="lastpage">
    			</span>
    		</div>
    	</div>
  </div>
</template>

<script>
import axios from 'axios';
//生成remove方法直接删除数组中的元素
Array.prototype.remove = function(val) {
	var index = this.indexOf(val);
	if (index > -1) {
	this.splice(index, 1);
	}
};

export default {
  	data(){
  		return{
  			userName:"",
  			usercode:"",
  			oldPassword:"",
  			newPassword:"",
  			reNewPassword:"",
  			modalShow:false,
  			choosenAll:false,
  			autoDeleteDay:0,
  			tableData:[],
  			pageSize:10,
  			pageNumber:1,
  			total:0,
  			start:"",
  			end:"",
  			requestUrl:"",
  			deleteTokenArray:[],
  			isDelete:false,
  			isAutoDelete:false,
  			isPasswordDivShow:false,
  			currentPage:1,
  		}
  	},
  	methods:{
  		searchTable(){
  			if(this.start == "" && this.end == ""){
  				this.initTable();
  			}else if(this.start != "" && this.end != ""){
  				this.initTable();
  			}else{
  				this.$message({
		            type: 'warning',
		            message: '时间必须都选或者都不选!'
	            });
  			}
  		},
  		init(){
  			this.userName = this.$route.query.userName;
  			this.requestUrl = this.$store.state.mainUrl;
  			this.initTable();
  			this.initIntervalJob();
  		},
  		//生成表格
  		initTable(){
  			let that = this;
  			let data = {
			  "usercode":this.usercode,
			  "starttime":this.start,
			  "endtime":this.end,
			  "currentPage":""+this.pageNumber+"",
			  "pageSize":""+this.pageSize+""
			}
			that.tableData = []; 
			this.request("search",data,function(res){
				that.pageNumber = res.data.pageNum;
				that.tableData = res.data.list;
				that.total = res.data.total;
				if(that.tableData.length === 0){
					that.choosenAll = false
				}
			})
  		},
  		//初始化定时任务时间
  		initIntervalJob(){
  			let that = this;
  			this.request("getcheduletimer","",function(res){
  				if(res.data.updatetype === 1){
  					that.isAutoDelete = true;
  				}else if(res.data.updatetype === 0){
  					that.isAutoDelete = false;
  				}
  				that.autoDeleteDay = res.data.day;
  			})
  		},
  		//显示隐藏的修改密码
  		showPasswordDiv(){
  			if(this.isPasswordDivShow === false){
  				this.isPasswordDivShow = true;
  				this.modalShow = true;
  			}else{
  				this.isPasswordDivShow = false;
  				this.modalShow = false;
  			}	
  		},
  		//重置密码
  		reset(){
  			if(this.newPassword == "" || this.reNewPassword == "" || this.oldPassword == ""){
  				this.$message({
	                showClose: true,
	                message:"密码必填!",
	                type: 'warning'
	            });
	            return
  			}
  			if(this.newPassword !==  this.reNewPassword ){
  				this.$message({
	                showClose: true,
	                message:"两个新密码必须一致!",
	                type: 'warning'
	            });
	            return
  			}
  			let data = {
			  	"username":this.userName,
			  	"password":this.newPassword,
			  	"oldpassword":this.oldPassword
			}	
			console.log(data)
			let that = this;
			this.request("repassword",data,function(res){
				that.isPasswordDivShow = false;
  				that.modalShow = false;
				that.$message({
		            type: 'success',
		            message: '修改成功!'
	            });
			})	
  		},
  		//选中checkbox 加载删除按钮
  		showDeleteBtn(e){
  			const index = e.target.value;
  			const isChecked = e.target.checked;
  			if(isChecked === true){
  				this.deleteTokenArray.push(this.tableData[index].token)
  			}else{
  				this.deleteTokenArray.remove(this.tableData[index].token)
  			}

  			if(this.deleteTokenArray.length > 0){
  				this.isDelete = true;
  				this.choosenAll = true;	
  			}else{
  				this.isDelete = false;
  				this.choosenAll = false;
  			}

  			if(this.deleteTokenArray.length === this.tableData.length){
  				this.choosenAll = true;
  			}else{
  				this.choosenAll = false;
  			}
  		},
  		//初始化tr td的间隔颜色
  		oddOrNot(num,state){
  			if(num%2 === 1){
  				if(state === 1){
  					return "background-f4-035 left"
  				}else if(state === 2){
  					return "background-f4-035 right"
  				}else{
  					return "background-f4-035"
  				}	
  			}else{
  				if(state === 1){
  					return "left"
  				}else if(state === 2){
  					return "right"
  				}else{
  					return ""
  				}	
  			}
  		},
  		//点击modal (模糊区域隐藏修改密码)
  		hideModal(e){
  			if(this.modalShow === true && this.isPasswordDivShow === true){
  				this.showPasswordDiv();
  			}
  		},
  		//点击自动删除
  		autoDelete(){
  			let that = this;
  			if(this.isAutoDelete === true){
  				this.modalShow = true;
	  			this.$confirm('本功能开启后，系统会定时删除'+ this.autoDeleteDay +'天及之前上传的文件，一旦删除，文件无法恢复，且每日会自动检索上传的文件，删除'+ this.autoDeleteDay +'天及之前的文件，确认要开启此功能？', '注意!', {
			        confirmButtonText: '确定',
			        cancelButtonText: '取消',
			        type: 'warning'
		        }).then(() => {
		        	this.modalShow = false;
		        	let data = {
					  "id":1,
					  "day":this.autoDeleteDay,
					  "updatetime":this.initDate(),
					  "updatetype":"1"
					}
		        	this.request("updatescheduletime",data,function(res){
		        		that.$message({
				            type: 'success',
				            message: '设置自动删除成功!'
			            });
		        	})
		           
		        }).catch(() => {
		        	that.modalShow = false;
		        	that.isAutoDelete = false
		            that.$message({
			            type: 'info',
			            message: '已取消自动删除'
		            });          
		        });
  			}else{
  				that.modalShow = false;
	        	let data = {
				  "id":1,
				  "day":this.autoDeleteDay,
				  "updatetime":this.initDate(),
				  "updatetype":"0"
				}
	        	this.request("updatescheduletime",data,function(res){
	        		that.$message({
			            type: 'success',
			            message: '设置禁用删除成功!'
		            });
	        	})
  			}
  		},
  		next(){
  			if(this.pageNumber >= (this.totalPage)){
  				return
  			}
  			this.pageNumber = this.pageNumber + 1;
  			this.initTable();
  		},
  		prev(){
  			if(this.pageNumber <= 1){
  				return
  			}
  			this.pageNumber = this.pageNumber - 1;
  			this.initTable();
  		},
  		homepage(){
  			if(this.pageNumber <= 1){
  				return
  			}
  			this.pageNumber = 1;
  			this.initTable();
  		},
  		lastpage(){
  			if(this.pageNumber >= (this.totalPage)){
  				return
  			}
  			this.pageNumber = this.totalPage;
  			this.initTable();
  		},
  		//判断页数输入并进行跳转或提示
  		checkType(e){
  			const val = e.target.value;
  			let regu = /^(0|\+?[1-9][0-9]*)$/;
  			if(val !== ""){
  				if(regu.test(val) === false){
  					this.$message({
			            type: 'warning',
			            message: '请输入正确的页数!'
		            });
  				}else if(regu.test(val) === true ){
  					if(val > this.totalPage){
  						this.pageNumber = this.currentPage;
  						this.$message({
				            type: 'warning',
				            message: '超过总页数!'
			            });
			            return
  					}else if(val <= this.totalPage){
  						this.currentPage = val;
  						this.pageNumber = val;
  						this.initTable();
  					}
  				}
  			}
  		},
  		//点击选中checkbox的删除
  		clickDeleteBtn(e){
  			let that = this;
  			this.modalShow = true;
  			const deleteString = this.deleteTokenArray.join(",");
  			this.$confirm('文件一旦删除，无法恢复，确认要删除选中的文件？', '注意!', {
		        confirmButtonText: '确定',
		        cancelButtonText: '取消',
		        type: 'warning'
	        }).then(() => {
	        	this.modalShow = false;
	        	let data = {
	        		token:deleteString
	        	}
	        	this.request('delete',data,function(res){
	        		that.deleteTokenArray.remove("token");
	        		that.initTable();
	        		that.$message({
			            type: 'success',
			            message: '删除成功!'
		            });
	        	})
	           
	        }).catch(() => {
	        	that.modalShow = false
	            that.$message({
		            type: 'info',
		            message: '已取消删除'
	            });          
	        });
  		},
  		//页头全选与反选
  		chooseAll(e){
  			if(e.target.checked === true){
  				this.isDelete = true;
  				this.choosenAll = true;
  				let chooseArray = document.getElementsByClassName("deleteClass");
  				this.deleteTokenArray = [];
  				for( let data of chooseArray ){
  					const i = data.value;
  					this.deleteTokenArray.push(this.tableData[i].token)
  					data.checked = true
  				}
  			}else{
  				this.isDelete = false;
  				this.choosenAll = false;
  				this.deleteTokenArray = [];
  				let chooseArray = document.getElementsByClassName("deleteClass")
  				for( let data of chooseArray ){
  					data.checked = false
  				}
  			}
  		},
  		//退出登录
  		exit(){
  			sessionStorage.removeItem("login");
  			this.$router.push("/login");
  		},
  		//合并请求
  		request(url,data,callback){
  			let that = this;
  			axios.post(this.requestUrl+url,data,{
  				headers:{
					"Content-Type":"application/json"
				}
  			}).then((res) => {
  				if(res.data.code == 100 ){
  					callback(res.data);
  				}else{
  					that.$message({
			            type: 'warning',
			            message: res.data.msg
			        });    
  				}
  			}).catch((err) => {
  				console.log(err)
  			})
  		},
  		//初始化文件大小
		initFileSize(fileSize) {
			var kb = 0;
			kb = Math.round(fileSize / 1024 * 100) / 100 + " KB";
			if(Math.round(fileSize / 1024 * 100) / 100 > 1024) {
				kb = Math.round(fileSize / 1024 / 1024 * 100) / 100 + " MB";
			}
			if(Math.round(fileSize / 1024 * 100) / 100 > 1024 * 1024) {
				kb = Math.round(fileSize / 1024 / 1024 / 1024 * 100) / 100 + " GB";
			}
			return kb
		},
		//初始化時間
		initDate() {
			var time = new Date();
			var year = time.getFullYear();
			var month = time.getMonth() + 1;
			if(month < 10) {
				month = "0" + month;
			}
			var day = time.getDate();
			if(day < 10) {
				day = "0" + day;
			}
			var minutes = time.getMinutes();
			if(minutes < 10) {
				minutes = "0" + minutes;
			}
			var hours = time.getHours();
			if(hours < 10) {
				hours = "0" + hours;
			}
			var second = time.getSeconds();
			if(second < 10) {
				second = "0" + second;
			}
			var now = year + '-' + month + '-' + day + " " + hours + ':' + minutes + ':' + second;

			return now
		},
		//生成格式
		initStandard(filename){
			return filename.split(".")[filename.split(".").length - 1];
		}
  	},
  	computed:{
  		startPage:function(){
  			if((this.pageSize * (this.pageNumber - 1) + 1) < 0){
  				return 0
  			}
  			return this.pageSize * (this.pageNumber - 1) + 1;
  		},
  		endPage:function(){
  			if((this.pageSize * this.pageNumber) < 0){
  				return 0
  			}
  			if( (this.pageSize * this.pageNumber) > this.total ){
  				return this.total
  			}
  			return this.pageSize * this.pageNumber;
  		},
  		totalPage:function(){
  			return Math.ceil(this.total/this.pageSize);
  		}
  	},
  	mounted(){
  		this.init();
  	}
}
</script>