<style type="text/css" scoped>
		.login{
      width: 100%;
      height: 100%;
      background-color: #fff;
      display: flex;
      justify-content:center;
   		 align-items: center;
    }
    .loginContainer img{
        width: 65px;
        height: 57px;
        display: inline-block;
        margin-bottom: 52px;
        margin-left: 40px;
    }
    .loginContainer .title{
      width: 195px;
      height: 36px;
      display: inline-block;
      font-size: 24px;
      vertical-align: top;
      margin-top: 10px;
    }
    .loginContainer input{
      width: 360px;
      height: 50px;
      display: inline-block;
      border: none;
      outline: none;
      border-radius: 10px;
      background-color: #F1F9FF;
      text-indent: 20px;
      font-size: 14px;
      color: #666666;
    }
    .loginContainer .radius{
      width: 362px;
      margin-top:7px;
      margin-bottom: 38px;
      border-radius: 10px;
      height: 52px;
      border: 1px solid #BCE0FD;
      background-color: #F1F9FF;
    }
    .forgotPassword{
      float: right;
      margin-top:5px;
      cursor: pointer;
    }
    .text{
      font-size: 14px;
      color: #999;
    }
    .loginBtn{
      width: 360px;
      height: 50px;
      border: 1px solid #0065CB;
      background-color: #0065CB;
      color: #fff;
      border-radius: 10px;
      font-size: 14px;
      line-height: 50px;
      text-align: center;
      margin-top: 56px;
      cursor: pointer;
    }
</style>

<template>
  <div class="login">
    	<div class="loginContainer">
        <div><img src="../assets/logo.png"><span class="title">国美小虫·文件云</span></div>
        <div><p class="text">用户名</p><div class="radius"><input type="text" placeholder="请输入账号" v-model="userName"></div></div>
        <div><p class="text">密码</p><div class="radius"><input type="password" placeholder="请输入密码" v-model="password">
          <!-- <p class="forgotPassword text">忘记密码？</p> -->
        </div></div>
        <div class="loginBtn" @click="login">登录</div>
      </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  	data(){
  		return{
  			password:"",
        userName:"",
        requestUrl:"",
  		}
  	},
  	methods:{
      login(){
        let data = {
          username:this.userName,
          password:this.password
        }
        if(this.userName === "" || this.password === ""){
          this.$message({
            showClose: true,
            message: "账号密码不能为空!",
            type: 'warning'
          });
          return
        }
        let that = this;
        axios.post(this.requestUrl+"login",data,{
          headers:{
            "Content-Type":"application/json"
          }
        }).then((res) => {
          if(res.data.code == 100){
            sessionStorage.setItem("login",true);
            this.$router.push({name:"index",query:{userName:this.userName,password:this.password}})
          }else if(res.data.code === 101){
              that.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              });
          }else{
            that.$message({
                showClose: true,
                message: res.data.msg,
                type: 'warning'
              });
          }
        })
      }
  	},
    mounted(){
      this.requestUrl = this.$store.state.mainUrl;
    }
}
</script>